<template>
  <div class="private-account-modal">
    <a-modal
      title="自有账号详情"
      :visible="visible"
      :maskClosable="maskClosable"
      @ok="handleOk"
      :confirmLoading="confirmLoading"
      @cancel="handleCancel"
      width="908px"
    >
      <!-- <a-button type="primary">
            <a-icon type="plus"/>新增
        </a-button> -->
      <form :form="form">
        <a-button
          style="width: 100%; margin-top: 8px; margin-bottom: 16px"
          type="dashed"
          icon="plus"
          @click="newMember"
        >新增成员</a-button>
        <a-table
          :columns="columns"
          :dataSource="data"
          :pagination="pagination"
          :scroll="{ x: 861}"
        >
          <!-- <template v-for="(col, i) in cols" :slot="col" slot-scope="text, record, index"> -->
          <template
            v-for="(col, i) in cols"
            :slot="col"
            slot-scope="text, record, index"
          >
              <a-input
                 v-if="record.editable"
                :key="col"
                :value="text"
                style="margin: -5px 0;min-width:90px"
                :placeholder="columns[i].title"
                @change="e => handleChange(e.target.value, record.key, col)"
              />
            <template v-else>{{ text }}</template>
          </template>
          <template
            slot="operation"
            slot-scope="text, record, index"
          >
            <template v-if="record.editable">
              <span v-if="record.isNew">
                <a @click="saveRow(record.key)">添加</a>
                <a-divider type="vertical" />
                <a-popconfirm
                  title="是否要删除此行？"
                  @confirm="remove(record.key)"
                >
                  <a>删除</a>
                </a-popconfirm>
              </span>
              <span v-else>
                <a @click="saveRow(record.key)">保存</a>
                <a-divider type="vertical" />
                <a @click="cancel(record.key)">取消</a>
              </span>
            </template>
            <span v-else>
              <a @click="toggle(record.key)">编辑</a>
              <a-divider type="vertical" />
              <a-popconfirm
                title="是否要删除此行？"
                @confirm="remove(record.key)"
              >
                <a>删除</a>
              </a-popconfirm>
            </span>
          </template>
        </a-table>
      </form>
    </a-modal>
  </div>
</template>

<script>
import AModal from "ant-design-vue/lib/modal";
import ATable from "ant-design-vue/lib/table";
import AButton from "ant-design-vue/lib/button";
import AIcon from "ant-design-vue/lib/icon";
import AForm from "ant-design-vue/lib/form";
import AInput from "ant-design-vue/lib/input";
import ADivider from "ant-design-vue/lib/divider";
import APopconfirm from "ant-design-vue/lib/popconfirm";
import ATooltip from "ant-design-vue/lib/tooltip"
import {addInsuranceAccount, editInsuranceAccount} from '@/api/calculateSetting'
const AFormItem = AForm.Item
export default {
  props:{
    activeCode:String
  },
  components: {
    AModal,
    ATable,
    AButton,
    AIcon,
    AForm,
    AFormItem,
    AInput,
    ADivider,
    APopconfirm,
    ATooltip
  },
  data() {
    return {
      maskClosable: false, //点击蒙层不关闭modal
      visible: false,
      confirmLoading: false,
      pagination: {
        showQuickJumper: true,
        pageSize: 5
      },

      // table
      columns: [
        {
          title: "账号状态",
          dataIndex: "status",
          key: "status",
          scopedSlots: { customRender: "status" }
        }
      ],
      data: [],
      cols: [""] //表头
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  methods: {
    editInsuranceAccountToServer(account){
      let _this = this 
      return new Promise((resolve,reject)=>{
        editInsuranceAccount(account).then((result) => {
          resolve(result)
        }).catch((err) => {
          reject(err)
        });
      })
    },
    addInsuranceAccountToServer(account){
      let _this = this
      return new Promise((resolve,reject) =>{
        addInsuranceAccount(account).then((result) => {
          resolve(result)
        }).catch((err) => {
          _this.$myNotification.sendFailedNotification(err)
          reject(err)
        });
      })
    },
    showModal(privateAccount) {
      this.visible = true;
      const account_param = privateAccount.account_param;
      const accounts = privateAccount.accounts;
      this.getColumns(account_param);
      this.getData(accounts);
    },
    getColumns(account_param) {
      for (let item in account_param) {
        this.columns.push({
          title: account_param[item].name,
          dataIndex: account_param[item].key,
          key: account_param[item].key,
          scopedSlots: { customRender: account_param[item].key }
        });
        this.cols.push(account_param[item].key);
      }
      this.columns.push({
        title: "操作",
        key: "operation",
        scopedSlots: { customRender: "operation" }
      });
    },
    getData(accounts) {
      accounts.forEach((item, index) => {
        item.key = index;
        item.editable = false;
      });
      this.data = accounts;
    },
    handleOk(e) {
      let _this = this;
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
      this.resetPrivateAccount();
      //重置
    },
    handleCancel(e) {
      this.visible = false;
      this.resetPrivateAccount();
    },

    handleSubmit(e) {
      e.preventDefault();
      this.validate();
    },
    newMember() {
      const newData ={
        editable: true,
        isNew: true,
        key: this.data.length + 1, //此处key暂时使用目前data的长度,待正式集成接口，data只会接收到当前页的数据，所以应使用所有数据的数量
        status:"",
      }
      this.cols.forEach(item=>{
        if(item!=''){
          newData[item] = ""
        }
      })
      this.data.unshift(newData);
    },
    remove(key) {
      const newData = this.data.filter(item => item.key !== key);
      this.data = newData;
    },
    saveRow(key) {
      const target = this.data.filter(item => item.key === key)[0];
      const account = {}
      account.insurance = this.$props.activeCode
      for(let key in target){
        if(key!='editable'&&key!='isNew'&&key!='key'&&key!='status'){
          if(key=='username'){
            account['account']  = target[key]
          }else{
            account[key] = target[key]
          }
        }
      }
      if(target.isNew){
        this.addInsuranceAccountToServer(account).then((result) => {
          target.editable = false;
          target.isNew = false;
        }).catch((err)=>{
          // this.data = this.data.filter(item => key !== item.key)
        })
      }else{
        this.editInsuranceAccountToServer(account).then((result) => {
          target.editable = false
        }).catch((err) => {
        });
      }
    },
    toggle(key) {
      const target = this.data.filter(item => item.key === key)[0];
      target.editable = !target.editable;
      this.data.sort((a,b)=>{return b-a}) //此处犹豫出现未知问题，当点击编辑按钮时需要刷新this.data才能使组件重新渲染成编辑状态，所以暂时用此方法
      // this.data[key].editable = !this.data[key].editable
    },
    getRowByKey(key, newData) {
      const data = this.data;
      return (newData || data).filter(item => item.key === key)[0];
    },
    cancel(key) {
      const target = this.data.filter(item => item.key === key)[0];
      target.editable = false;
      this.data.sort((a,b)=>{return b-a}) //此处犹豫出现未知问题，当点击取消编辑按钮时需要刷新this.data才能使组件重新渲染成查看状态，所以暂时用此方法
    },
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },

    resetPrivateAccount() {
      this.columns = [
        {
          title: "账号状态",
          dataIndex: "status",
          key: "status",
          scopedSlots: { customRender: "status" }
        }
      ];
      this.cols = [""];
      this.$emit("resetPrivateAccount");
    }
  }
};
</script>

<style>
</style>



