<template>
  <div class="complicated-card-list" ref="content">
    <a-spin :spinning=loading>
    <a-list :grid="{ gutter: 24,xxl:3,xl:2,md:2, sm: 1 }" :dataSource="dataSource">
      <a-list-item slot="renderItem" slot-scope="item, index">
        <a-card :hoverable="true">
          <a-card-meta>
            <div style="margin-bottom: 3px" slot="title">{{ item.name }}</div>
            <div style="margin-bottom: 3px;flex: 1;text-align: right;" slot="title">
              <a-icon  :style="{color:item.isDefault ? '#52c41a':'rgba(0, 0, 0, 0.45)'}" style="font-size:20px" type="check-circle" />
            </div>
            <a-avatar class="card-avatar" slot="avatar" :src="item.avatar" size="small" />
            <div class="meta-cardInfo" slot="description">
              <div>
                <p>可用自有账号数目</p>
                <p>{{ item.privateAccountNum | NumberFormat }}</p>
              </div>
              <div>
                <p>临时账号</p>
                <div>
                  <a-tooltip :title="item.activeUser">
                    <span>{{ item.activeUser }}</span>
                  </a-tooltip>
                  <div>
                    <a-badge status="success" style="margin-left:10px" />
                    <span style="font-size:14px">正常</span>
                  </div>
                </div>
              </div>
            </div>
          </a-card-meta>
          <template class="ant-card-actions" slot="actions">
                <a @click="showAccountModal(item.code)">
                  自有账号详情
                </a>
                <a :id="item.id" @click="e => setDefault(e)">{{item.isDefault?'取消设置默认接口':'设置默认接口'}}</a>
                <!-- <a>
                  <a-icon type="share-alt"/>
                </a> -->
                <!-- <a>
                  <a-dropdown>
                    <a class="ant-dropdown-link" href="javascript:;">
                      <a-icon type="ellipsis"/>
                    </a>
                    <a-menu slot="overlay">
                      <a-menu-item>
                        <a href="javascript:;">1st menu item</a>
                      </a-menu-item>
                      <a-menu-item>
                        <a href="javascript:;">2nd menu item</a>
                      </a-menu-item>
                      <a-menu-item>
                        <a href="javascript:;">3rd menu item</a>
                      </a-menu-item>
                    </a-menu>
                  </a-dropdown>
                </a> -->
</template>
        </a-card>
      </a-list-item>
    </a-list>
    <private-account-modal v-if="this.privateAccounts.length" :activeCode = "activeCode"  @resetPrivateAccount="resetPrivateAccount" ref="dialogtreeAccountModal"/>
    </a-spin>
  </div>
</template>

<script>
  import AList from 'ant-design-vue/lib/list'
  import ASpin from 'ant-design-vue/lib/spin'
  import AIcon from 'ant-design-vue/lib/icon'
  import ACard from 'ant-design-vue/lib/card'
  import AAvatar from 'ant-design-vue/lib/avatar'
  import ATooltip from 'ant-design-vue/lib/tooltip'
  import ABadge from 'ant-design-vue/lib/badge'
  import PrivateAccountModal from './PrivateAccountModal.vue'
  import { getCalculateList,getCalculateDetail,setDefaultInsurance } from '@/api/calculateSetting'
  const AListItem = AList.Item
  const ACardMeta = ACard.Meta
  

  // const dataSource = []
  // for (let i = 0; i < 8; i++) {
  //   dataSource.push({
  //     id:'00'+i,
  //     title: i%2?'四川国寿财0'+i:'四川新太平洋0'+i,
  //     isDefault:i==0?true:false,
  //     avatar: 'https://gw.alipayobjects.com/zos/rmsportal/zOsKZmFRdUtvpqCImOVY.png',
  //     activeUser: 710105136666,
  //     privateAccountNum: i
  //   })
  // }

  export default {
    name: 'Article',
    components: {
      AList,
      AListItem,
      ASpin,
      AIcon,
      ACard,
      ACardMeta,
      AAvatar,
      ATooltip,
      ABadge,
      PrivateAccountModal
    },
    data() {
      return {
        dataSource:[],
        loading:false,
        privateAccounts:[],
        activeCode:'',
        privateAccount:{}
      }
    },
    created(){
      let _this = this
      this.loading = true
      this.getCalculateListFromServer().then((result) => {
        _this.dataSource.forEach((item,index) => {
          let _item = item
          this.getCalculateDetailFromServer(item.code).then((result) => {
            _item.privateAccountNum = result.accounts.length
            _this.privateAccounts.push(result)
            if(index == _this.dataSource.length-1){
              _this.loading =false
            }
          })
        })
      })
    },
    methods: {
      sendSetDefaultInsuranceToServer(code){
        let _this = this 
        return new Promise((resolve,reject)=>{
          setDefaultInsurance({insurance:code}).then((result)=>{
            resolve(result)
          }).catch((err)=>{
            _this.loding =false
            _this.$myNotification.sendFailedNotification(err)
            reject(err)
          })
        })
      },
      getCalculateListFromServer(){
        let _this = this
        return new Promise((resolve,reject)=>{
          getCalculateList().then((result) => {
            result.data.forEach((item,index) => {
              _this.dataSource.push({
                id: index,
                name: item.name,
                isDefault: item.default,
                code:item.code,
                avatar: 'https://gw.alipayobjects.com/zos/rmsportal/zOsKZmFRdUtvpqCImOVY.png',
                activeUser: 710105136666,
                privateAccountNum: index
              })
              resolve(result)
            })
          }).catch((err) => {
            _this.loding =false
            _this.$myNotification.sendFailedNotification(err)
            reject(err)
          });
        })
      },
      getCalculateDetailFromServer(insurance){
        return new Promise((resolve,reject) => {
          getCalculateDetail({insurance:insurance}).then((result) => {
            resolve(result)
          }).catch((err) => {
            _this.loding =false
            _this.$myNotification.sendFailedNotification(err)
            reject(err)
          });
        })
      } ,
      showAccountModal(code) {
        this.privateAccount = this.privateAccounts.filter(item=>item.code == code)[0]||{}
        this.activeCode = code
        this.$refs.dialogtreeAccountModal.showModal(this.privateAccount)
      },
      setDefault(e) {
        const targetID = e.target.getAttribute('id')
        const targetElm = this.dataSource.filter(item => item.id == targetID)
        const defaultElm = this.dataSource.filter(item => item.isDefault == true)
        if (targetElm.length != 0 && targetElm[0].isDefault!=defaultElm[0].isDefault) {
          this.sendSetDefaultInsuranceToServer(targetElm[0].code||"").then((result) => {
            defaultElm[0].isDefault = false
            targetElm[0].isDefault = !targetElm[0].isDefault
            this.$notification['success']({
                  message: '已将'+targetElm[0].name+'设置为默认算价器接口',
                })
          })
        }
      },
      resetPrivateAccount(){
        this.privateAccount = {}
      }
    }
  }
</script>

<style lang="scss" scoped>
  .complicated-card-list {
    .meta-cardInfo {
      zoom: 1;
      margin-top: 16px;
      >div {
        position: relative;
        text-align: left;
        float: left;
        width: 50%;
        >p {
          color: rgba(0, 0, 0, .45);
          font-size: 12px;
          line-height: 20px;
          margin-bottom: 4px;
          &:nth-child(2) {
            line-height: 32px;
            font-size: 22px;
            margin: 0;
            overflow: hidden;
          }
        }
        >div {
          display: flex;
          >span {
            line-height: 32px;
            font-size: 22px;
            margin: 0;
            flex: 1;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          >div {
            width: 60px;
            margin: 0 10px;
            line-height: 32px;
          }
        }
      }
    }
  }
</style>

<style>
  .insurance-settings .complicated-card-list ul.ant-card-actions>li {
    margin: 0
  }
  
  .insurance-settings .complicated-card-list ul.ant-card-actions>li>span {
    width: 100%
  }
  
  .insurance-settings .complicated-card-list ul.ant-card-actions>li>span>a {
    padding: 12px 0;
  }
  .insurance-settings .complicated-card-list .ant-card-body .ant-card-meta-title{
    display: flex
  }
</style>



